import Vue from 'vue'
import Router from 'vue-router'
import Layout from '@/pages/layout'
import Product from '@/pages/product'
import Home from '@/pages/product/home'
import Hongkong from '@/pages/product/hongkong'
import Business from '@/pages/product/business'
import International from '@/pages/product/international'
import User from '@/pages/user'
import Information from '@/pages/information'

Vue.use(Router)

export default new Router({
  // mode:'history',
  linkActiveClass: 'active',
  linkExactActiveClass: 'active',
  routes: [
    {
      path: '/',
      name: 'Layout',
      component: Layout
    },
    {
      path: '/product',
      name: 'Product',
      redirect:"product/home",
      component: Product,
      children:[
        {
          path:"home",
          name:"home",
          component:Home
        },
        {
          path:"hongkong",
          name:"Hongkong",
          component:Hongkong,
        },
        {
          path:"international",
          name:"international",
          component:International
        },
        {
          path:"business",
          name:"Business",//注意路由路径的引号内不得里空格；否则可能渲染不成功。
          component:Business
        }
      
      ]
    },
    {
      path: '/user',
      name: 'user',
      component:User
    },
    {
      path: '/information2',
      name: 'Information',
      component:Information
    },
    // {
    //   path: '/test',
    //   name: 'test',
    //   component: resolve => require(['@/components/test'],resolve)//懒加载
    // }
  ]
})



