<template>
  <div class="info_box">
    <div class="head ">

      <slot name="head_slot" />
      <slot name="head_right" />

    </div>
    <div class="body">
      <slot name="body_slot" />
    </div>
  </div>
</template>
<script>
export default {
  name: "Infobox",
  data() {
    return {};
  }
};
</script>

<style lang="scss">
.info_box {
  margin-bottom: 10px;
  width: 100%;
  background-color: #fff;
  .head {
    display: flex;
    padding: 0 10px;
    justify-content: space-between;
    aside {
      margin: 15px 0;
    }
    .head_left {
      border-left-style: solid;
      border-left-width: 3px;

      color: #333;
      padding-left: 9px;
    }
    .head_right {
      a {
        color: #888;
        font-size: 12px;
      }
    }
  }
}
</style>




