<template>
  <div class="news2">
    <ul id="news">

      <li v-for="( news2, index ) in newslist2" :key="index">
        <a :href="news2.link">
          <aside class="left">
            <p class="p1" v-text="news2.title"></p>
            <p class="p2">
              <span class="date">{{news2.view}}次学习</span>
              <span class="free">免费</span>
              <!-- <span class="browse"> <img src="http://cdn.shangrongfintech.com/static/index/images/browse.png" alt="">536</span> -->
            </p>
          </aside>
          <aside class="right">
            <img :src="news2.img" alt="">
          </aside>

        </a>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  props:{
newslist2:{
  type:Array,
       default: function () {
        return [ {
          "link":"www.baidu.com",
    "img":"http://admin.shangrongfintech.com/static/logo/upload/15300622105b32e582c19d7.jpeg",
    "title":"上半年港IPO数量全球居首 全年或集资2000亿港币；美国批准首款大麻衍生药物 用于治疗小儿癫痫",
    "view":"362"
    
  
    }]    
  }
     }

  },
  methods: {}
};
</script>

<style lang='scss' scoped>
ul li {
  display: flex;
  border-bottom: 1px solid #e6e6e6;
  justify-content: space-between;
  padding: 20px 10px;
  background-color: #fff;

  .left {
    float: left;
    width: 65.5%;
    height: 71.5px;
    .p1 {
      font-size: 13px;
      color: #333333;
      line-height: 20px;
      word-break: break-all;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
      overflow: hidden;
      .browse img {
        width: 14px;
        height: 8px;
        vertical-align: inherit;
      }
    }
    .p2 {
      margin-top: 15px;
      display: flex;
      justify-content: space-between;
      .free{
        color: #36e700;
    // f   loat: right;
        font-size: 12px;
      }
      span {
        font-size: 12px;
      }
      .browse {
        padding-left: 20px;
        img {
          width: 14px;
          height: 8px;
          vertical-align: inherit;
        }
      }
    }
  }
  .right {
    float: right;
    width: 29.5%;
    height: 71.5px;
    img {
      width: 100%;
      height: 100%;
    }
  }
}
</style>



