<template>

    <div class="my_tab_bar">
      <router-link to="/" class="tabbar_item" exact>
        <i class="iconfont icon-weibiaoti1"></i>  
         <p class="tabbar__label">首页 </p>
      </router-link>
      <router-link to="/product" class="tabbar_item">
        <i class="iconfont icon-msnui-product"></i>  
         <p class="tabbar__label">产品 </p>
      </router-link>
      <router-link to="/user" class="tabbar_item">
        <i class="iconfont icon-wodemian2"></i>  
         <p class="tabbar__label">我的 </p>
      </router-link>

        <!-- <router-link v-for="item in tab_list" class="tabbar_item" :key="item.id" :to="item.path">
            <i class="iconfont"  :class="['icon-'+item.class1]"   ></i>
            <p class="tabbar__label">{{item.title}} </p>
        </router-link> -->

    </div>
</template>
<script>

export default {
  name: "menuTab",
  data() {
    return {
     
    };
  }
};
</script>
<style lang="scss">
.my_tab_bar {
  position: fixed;
  width: 100%;
  min-width: 320px;
    max-width: 768px;
    width: 100%;
  display: flex;
  justify-content: space-around;
  z-index: 999999;
  bottom: 0;
  margin-top: 5px;
  background: #fff;
  border-top: 1px solid #f2f2f2;

  .tabbar_item {
    padding: 7px 0 2px;
    font-size: 0;
    color: #999;
    text-align: center;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    .tab_icon,.iconfont {
      display: inline-block;
      font-size: 30px;
    }
  }
  .tabbar__label {
    text-align: center;
    color: #999;
    font-size: 10px;
    line-height: 1.8;
    padding-top: 6px;
    margin: 0;
   
  }
.active p, .active .iconfont{
     color: #65cfc9;
    }
// .exact p, .exact .iconfont{
//      color: #65cfc9;
//     }
  img {
    width: 100%;
  }
}


</style>



