import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
const state = {
  userInfo:{
    name:"游客",
    role:"客户"
    
  },
  notes: [],
  activeNote: {},
  iShow:true,
  count:1
};

const mutations = {
  // increment () {
  //   state.count ++
   
  // },
  close(){
    state.iShow = false
  }
}
const actions ={     //用于异步函数操作
//   incrementAsync({ commit }) {
//     alert("ddd")
//     setTimeout (() => {
//         commit('increment');
//     }, 1000);
// },
close({commit}){
  commit('close')
}
}
export default new Vuex.Store({
  state,
  mutations,
  actions
})


