<template>
  <div class="pro_main">
  <div class="pro_sub_tab">
      <ol>
        <li :class="{active:Select==index+1}" v-for="(item,index) in sublist"  :key="item.id" @click="start(index)">
          <span> {{item.title}} </span>
        </li>
     
      </ol>
    </div>
    <component :is="'Select0'+Select"></component>
  <!-- <div class="pro_con">
    <ul><li  v-for="item in tablelist" :key="item.id"><router-link :to="item.link"><img :src="item.imgUrl"></router-link></li></ul>
  </div> -->
  </div>
</template>
<script>
import Select01 from '@/pages/product/hongkong/select1'
import Select02 from '@/pages/product/hongkong/select2'
import Select03 from '@/pages/product/hongkong/select3'
export default {
  name:"hongkong",
  data(){

    return{
      Select:1,
sublist:[
  {
    "title":"危机保障",
    "path":"../../pages/test"
  },
  {
    "title":"储蓄分红",
    "path":"../../pages/test"
  },
  {
    "title":"人寿保险",
    "path":"../../pages/test"
  },
],

    }
  },
  methods:{
    start(index){
      this.Select=index+1;
      // console.log(index)
    }
  },
  components:{
    Select01,
    Select02,
    Select03,
  }
};
</script>
<style lang="scss">
.pro_sub_tab{
  background-color: #fff;
  ol{
    display: flex;
    padding:10px 0;
     justify-content: space-evenly;
  li{
    border:1px solid  #999999;
    border-radius: 30px;
    background-color: transparent;
    font-size: 12px;
    padding: 4px 15px 4px 15px;
    color: #999999;
    &.active{
        background-color: #d1f6f4ad;
           border:1px solid #22dcd0;
               color:#25e0b6;
    }
  }
  }
}
.pro_con{
  ul{
    background-color: #fff;
    font-size: 0;
    li:nth-child(2n) a{
      border-left: 1px solid #eee;
    }
    li:last-child  a{
      // background:red;
      border-left:1px solid transparent;
    }
    
    li{
      // background-color: #fff;
     
      width:49%;
      width:calc(50% - 1px);
display: inline-block;
    }
  
    a{
          text-align: center;
       border-bottom: 1px solid #eee;
  display: block;
  padding:55px;
  img{
  width: 75px;
  height: 50px;
  }
}
  }
}
</style>



