<template>
  <div class="modal" v-show="iShow" @touchmove.prevent>
    <transition name="mod">
      <div class="modal_content" v-if="iShow">
        <div class="bg"></div>
        <!-- <img src="http://cdn.shangrongfintech.com/static/images/tanchuangbg1.png" alt=""> -->
        <!-- <slot name="modal_main"></slot> -->
        <span class="close" @click="closeMe">&times;</span>
        <form>
          <div class="input-bank">
            <label for="user">姓名</label>
            <input id="user" type="text" maxlength="16" v-model="userInfo.name" placeholder="请输入姓名">
          </div>
          <div class="input-bank">
            <label for="tel">手机</label>
            <input id="tel" v-model="userInfo.tel" type="text" maxlength="11" :class="{erro:erro}" placeholder="请输入电话">
            <span class="send_code">
              <span v-if="sent">重新发送({{timer}})</span>
              <span v-else @click.self="sendCode">发送验证码</span>
            </span>
          </div>
          <div class="input-bank">
            <label for="code">验证码</label>
            <input @keyup.enter="submit" id="code" type="text" maxlength="6" v-model="userInfo.code" placeholder="请输入验证码">
          </div>
          <a @click="submit" class="button">提交</a>
        </form>
      </div>
    </transition>
    <div class="modal_dialog" v-show="iShow"></div>

  </div>
</template>
<script>
export default {
  name: "Dialog",
  data() {
    return {
      iShow: false,
      send_code: "",
      sent: false,
      timer: null,
      erro: false,
      notice: "",
      username: "",
      userInfo: { name: "", tel: "", code: "" }
    };
  },
  created() {
    this.iShow = this.$store.state.iShow;
    const session = sessionStorage.getItem("dialog");
    if (session === null) {
      setTimeout(() => {
        // alert("加载完成")

        this.iShow = true;
        // this.iShow = session
      }, 1000);
    } else {
      this.iShow = eval(session); //将字符串false 用eval() 转成 布尔 false
      //console.log(this.iShow)
    }
  },
  // props:{
  //  iShow:{//接受组件的传值
  //   type:Boolean, //传值的数据类型
  //   default:true,//在为收到父组件传值时，组件的默认值。
  //   }

  // },
  methods: {
    closeMe() {
      this.iShow = false;
      // this.$store.dispatch('close')
      sessionStorage.setItem("dialog", this.iShow); //默认以字符串的的数据类型储存在回话内存里。
    },
    submit() {
      var user_name = this.userInfo.name.trim(); //trim() 去除字符串前后可能存在的空格
      var user_name = this.userInfo.name.trim();
      var user_tel = this.userInfo.tel.trim();
      var user_code = this.userInfo.code.trim();
      if (user_name === "") {
        //  alert("请输入姓名")
        // this.$toast('请输入姓名')

        this.$toast({
          message: "请输入姓名",
          duration: 3000,
          position: "middle"
        });
        // this.$Indicator.open('加载中...');

        return;
      } else if (user_tel === "" || user_tel === null) {
        //  alert("请输入电话")
        this.$toast("请输入电话");

        return;
      } else if (user_tel !== "") {
        var mobileCheck = /^1[2|3|4|5|6|7|8|9]\d{9}$/;
        var isPhone = mobileCheck.test(user_tel);
        // console.log(isPhone);
        if (!isPhone) {
          this.$toast("此手机号码格式有误");
          // alert("手机号格式不正确")

          return;
        } else if (user_code === "5203") {
          this.$toast({
            message: "正在登录中..",
            duration: 2500
          });
          setTimeout(() => {
            this.iShow = false;

            sessionStorage.setItem("dialog", this.iShow);
            localStorage.setItem("user_name", user_name);

            this.$router.push({
              path: "/user/" + "?t=" + new Date().getTime()
            });
          }, 3000);
        } else {
          this.$toast("请输入正确验证码");
        }
      }
    },
    sendCode() {
      var that = this; //这里this指向实例vue,先把this的指向储存给that

      this.timer = 60;
      var user_tel = this.userInfo.tel.trim();
      var mobileCheck = /^1[2|3|4|5|6|7|8|9]\d{9}$/; //检查手机号码格式的正则表达式
      var isPhone = mobileCheck.test(user_tel); //
      console.log(isPhone);
      if (!isPhone) {
        // alert("手机号格式不正确")
        this.$toast("手机号格式不正确");

        // this.erro = true;
        return;
      } else {
        that.sent = true;
        var settime = setInterval(function() {
          //此处的this 指向的是windows,和上面的this是两回事
          //lert(this)  // => [object Window]
          that.timer--;
          // console.log(that.timer)
          if (that.timer === 0) {
            clearInterval(settime);
            that.sent = false;
            that.timer = "";
          }
        }, 1000);
      }
    }
  }
};
</script>
<style lang="scss">
.mod-enter-active {
  transition: all 0.8s ease;
}
.mod-leave-active {
  transition: all 0.5s ease;
}
.mod-enter {
  transform: translateY(-500px);
  opacity: 0;
}
.mod-leave-active {
  transform: translateY(-500px);
  opacity: 0;
}
.modal_dialog {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  z-index: 999;
  transition: all 1s;
}
.modal {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 999;

  transition: all 0.5s;
  .modal_content {
    max-width: 768px;
    // min-width:320px;
    width: 80%;
    height: 70%;
    background: #fff;
    position: absolute;
    top: 14%;
    left: 0;
    right: 0;
    margin: auto;
    text-align: center;
    box-sizing: border-box;
    border-radius: 5px 5px 5px 5px;
    .bg{
      background: url('/static/images/tanchuangbg1.png') no-repeat top center/cover;
      height: 40%;
      }

    .close {
      width: 50px;
      height: 50px;
      position: absolute;
      top: 0;
      right: 0;
      font-size: 32px;
      color: #fff;
      font-weight: 100;
    }
    img {
      width: 100%;
    }
  }
}
form {
  padding: 0 20px;
  margin: 0 auto;
  background-color: #fff;
}
.input-bank {
  position: relative;
  text-align: left;
  height: 55px;
  line-height: 55px;
  border-bottom: 1px solid #eee;
  .send_code {
    line-height: 28px;
    border: 1px solid #53e8ee;
    border-radius: 8px;
    text-align: center;
    color: #53e8ee;
    padding: 0 8px;
    position: absolute;
    top: 14px;
    right: 0;
    font-size: 12px;
  }
  input {
    padding: 0;
    width: 40%;
    border: none;
    height: 30px;
    outline: none;
    margin: 0 0 0 25%;
    font-size: 16px;
  }
  label {
    position: absolute;
    font-size: 16px;
  }
}
.button {
  width: 80%;
  display: inline-block;
  margin-top: 10%;
  height: 36px;
  line-height: 36px;
  background-color: #68dcd9;
  border-radius: 5px 5px 5px 5px;
  color: #fff;
  font-size: 18px;
  text-align: center;
}
.erro {
  color: red;
}
</style>



