<!-- The ref attr used to find the swiper instance -->
<template>
  <swiper :options="swiperOption" ref="mySwiper">
    <!-- slides -->
    <swiper-slide class="slider" v-for=" (item,index) in sliderlist" :key="index"><a :href="item.url"><img :src="item.pic" alt=""></a></swiper-slide>
    <!-- Optional controls -->
    <div class="swiper-pagination" slot="pagination"></div>
  </swiper>
</template>

<script>
export default {
  name: "carrousel",
  data() {
    return {
      swiperOption: {
        loop: true,
        autoplay: {
          delay: 2000,
          disableOnInteraction: false //用户操作swiper之后，是否禁止autoplay。默认为true：停止。
        },
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        }
      }
    };
  },
  props: {
    sliderlist: {
      type: Array,
      default: function() {
        return [
          {
            url: "http://www.baidu.com",
            pic:
              "http://admin.shangrongfintech.com/upload/banner/5a2f9624e68fb.png"
          },
          {
            url: "http://www.baidu.com",
            pic:
              "http://admin.shangrongfintech.com/upload/banner/5a2f9624e68fb.png"
          }
          
        ];
      }
    }
  },
  computed: {
    swiper() {
      return this.$refs.mySwiper.swiper;
    }
  },
  mounted() {
    // current swiper instance
    // 然后你就可以使用当前上下文内的swiper对象去做你想做的事了

    this.swiper.slideTo(3, 1000, false);
  }
};
</script>
<style lang="scss">
.slider {
  img {
    width: 100%;
  }
}
</style>


