<template>
  <div id="app">
    <router-view/>
      <!-- <menuTab/> -->
      <Dialog/>
       <div class="stop">
      <span class="line">我是有底线的</span>
    </div>
  </div>
</template>
<script>
import menuTab from "@/components/menuTab";
import Dialog from "@/components/dialog";
export default {
  name: 'App',
  components:{
     menuTab,
     Dialog
  }
}
</script>

<style lang="scss">
#app{
  margin:0 auto;
  min-width: 320px;
  max-width: 768px;
}

body{
  background:#f9f8f8;
  -webkit-tap-highlight-color:transparent;
}
ul,ol,p{
  padding:0;
  margin:0
}
li{
  list-style: none;
}
p{
  line-height: 1.5
}
a{
    color: #888;
    text-decoration: none;
    -webkit-tap-highlight-color: transparent;
}
.stop{
padding: 50px 0 20px 0;
  color:#a7a7a7;
  text-align: center;
  .line{
    font-size: 12px;
    display:inline-block;
    position: relative;
   &::before{
     position: absolute;
    left: -100px;
    top: 50%;
      content:"";
    display: block;
    height:1px;
    width:90px;
    background-color: #d4d4d4;
   }
   &::after{
     position: absolute;
     top:50%;
     right:-100px;
         content:"";
    display: block;
    height:1px;
    width:90px;
    background-color: #d4d4d4;
   }
  }
}
</style>



