<template>
  <div>
   <div class="pro_sub_tab">
      <ol>
        <li :class="{active:Select==index+1}" v-for="(item,index) in sublist"  :key="item.id" @click="start(index)">
          <span> {{item.title}} </span>
        </li>
     
      </ol>
    </div>
    
 <component :is="'Select0'+Select"></component>
  </div>
</template>
<script>
import Select01 from '@/pages/product/business/select1'
import Select02 from '@/pages/product/business/select2'
export default {
  name: "Bussiness",
  data() {
    return {  
      Select:1,
      sublist: [
        {
          title: "企业上市",
          path: "../../pages/test"
        },
        {
          title: "企业融资",
          path: "../../pages/test"
        }
      ],
     
    };
  },methods:{
    start(index){
      this.Select=index+1;
      // console.log(index)
    }
  },
  components: {
     Select01,
    Select02,
  }
};
</script>
<style lang="scss">
.pro_sub_tab{
      margin-bottom: 10px;
  background-color: #fff;
  ol{
    display: flex;
    padding:10px 0;
     justify-content: space-evenly;
  li{
    border:1px solid  #999999;
    border-radius: 30px;
    background-color: transparent;
    font-size: 12px;
    padding: 4px 15px 4px 15px;
    color: #999999;
    &.active{
        background-color: #d1f6f4ad;
           border:1px solid #22dcd0;
               color:#25e0b6;
    }
  }
  }
}



</style>



