<template>
  <div class="news1">
    <ul id="news">

      <li v-for=" news1 in newslist1" :key="news1.id">
        <a :href="news1.link">
          <aside class="left">
            <p class="p1" v-html="news1.title"></p>
            <p class="p2">
              <span class="date">{{news1.date}}</span>
              <span class="browse"> <img src="http://cdn.shangrongfintech.com/static/index/images/browse.png" alt="">{{news1.view}}</span>
            </p>
          </aside>
          <aside class="right">
            <img :src="news1.img" alt="">
          </aside>

        </a>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
   props:{
newslist1:{
  type:Array,
       default: function () {
        return [ {
          "link":"http://baidu.com",
    "img":"http://admin.shangrongfintech.com/static/logo/upload/15300622105b32e582c19d7.jpeg",
    "title":"上半年港IPO数量全球居首<span style='color:red'>全年</span>或集资2000亿港币；美国批准首款大麻衍生药物 用于治疗小儿癫痫",
     "date":"2018-06-14",
    "view":"265"
  
    }]

       
      
  }
     }

  },
  methods: {}
};
</script>

<style lang='scss' scoped>
ul li {
  display: flex;
  border-bottom: 1px solid #e6e6e6;
  justify-content: space-between;
  padding: 20px 10px;
  background-color: #fff;

  .left {
    float: left;
    width: 65.5%;
    height: 71.5px;
    .p1 {
      font-size: 13px;
      color: #333333;
      line-height: 20px;
      word-break: break-all;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
      overflow: hidden;
      .browse img {
        width: 14px;
        height: 8px;
        vertical-align: inherit;
      }
    }
    .p2 {
      margin-top: 15px;
      display: flex;
      justify-content: space-between;
      span {
        font-size: 12px;
      }
      .browse {
        padding-left: 20px;
        img {
          width: 14px;
          height: 8px;
          vertical-align: inherit;
           margin-right: 5px;
        }
      }
    }
  }
  .right {
    float: right;
    width: 29.5%;
    height: 71.5px;
    img {
      width: 100%;
      height: 100%;
    }
  }
}
</style>



