<template>
  <div class="in_content">
      <ul>
        <li v-for="item in contentlist.data" :key="item.id">
          <img :src="item.logo" alt="">
          <div class="info">
            <p> {{item.name}} </p>
            <p class="time">
              <span> 期限：{{item.proage}}  </span> &nbsp;
              <span>起投资金 {{item.price}} </span>
            </p>
            <div class="classify">
              {{item.type_id}}
            </div>
          </div>
        </li>
      </ul>
    </div>
</template>
<script>
export default {
  name:"Select2",
  data(){
    return{
 contentlist: {
        code: 200,
        msg: "OK",
        data: [
         {
		"id": "40",
		"type_id": "信用证书服务",
		"logo": "http:\/\/admin.shangrongfintech.com\/upload\/product\/5a0bdc311590f.jpg",
		"name": "信用证书服务",
		"price": "100万",
		"proage": "100"
	}, {
		"id": "26",
		"type_id": "证券大宗交易咨询服务",
		"logo": "http:\/\/admin.shangrongfintech.com\/upload\/product\/598a7fcda5803.png",
		"name": "股权质押",
		"price": "100000.00",
		"proage": "1000"
	}
        ]
 }
 }
  }

}
</script>
<style lang="scss">
.in_content {
  background-color: #fff;
  li {
    border-top: 1px solid #eee;
    padding: 10px;
  }
  .info {
    position: relative;
    p {
      font-size: 15px;
      line-height: 1.5;
    }
    .time {
      font-size: 12px;
      color: #333;
    }
  }
  .classify {
    position: absolute;
    bottom: 10px;
    right: 0;
    padding: 2px 4px;
    border-radius: 5px;
    font-size: 12px;
    border: 1px solid #ff5a0b;
  }
  img {
    width: 100%;
    height: 205px;
  }
}
</style>





