<template>
  <div>
    <div class="product_top_tab">
      <ul>
        <li>
          <router-link  tag="span" to="/product/home" >国内产品  </router-link>
        </li>
        <li>
           <router-link tag="span" to="/product/hongkong" >香港保险</router-link>
        </li>
        <li>
           <router-link tag="span" to="/product/international">国际产品</router-link>
        </li>
        <li>
           <router-link tag="span" to="/product/business">企业金融</router-link>
        </li>
      </ul>
    </div>
    <router-view/>
    <menuTab/>
    </div>
      

    </template>
    <script>
    import menuTab from "@/components/menuTab"
    export default {
      name:"Product",
      data(){
        return{

        }
        },components:{
          menuTab
        }
      
    }
    </script>
    <style lang="scss">
$green: #54c5ff;
$yellow: #ff9600;
$red: #f52323;
$blue: #00aeff;
$white: #fff;
.product_top_tab {
  background-color: #fff;
  border-top: 1px solid #e6e6e6;
  border-bottom: 1px solid #e6e6e6;
  ul {
    display: flex;
    padding: 10px 0 10px 0;
    justify-content: space-around;
    li {
      text-align: center;
      width: 25%;
      color: #999999;
      font-size: 14px;
      span {
        position: relative;
        padding-bottom: 5px;
      }
      .active {
        font-size: 16px;
        color: #66e1d9;
        &::after {
          position: absolute;
          bottom: 0;
          left: 18px;
          content: "";
          width: 30px;
          height: 3px;
          background-color: #66e1d9;
          border-radius: 1.5px;
        }
      }
    }
  }
}


    </style>
    
    
    


